m: uppercase;\r\n  }\r\n}","$darkShade100: #1f2041;\r\n\r\n$darkShade75: rgba(31, 32, 65, 0.75);\r\n\r\n$darkShade50: rgba(31, 32, 65, 0.5);\r\n\r\n$darkShade25: rgba(31, 32, 65, 0.25);\r\n\r\n$darkShade5: rgba(31, 32, 65, 0.05);\r\n\r\n$purple: #bc9cff;\r\n\r\n$green: #6fcf97;\r\n\r\n$gradient: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);\r\n\r\n$gradientSecond: linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%); \r\n\r\n$font-family: Montserrat, Arial, sans-serif;",".button {\r\n  position: relative;\r\n  z-index: 0;\r\n  padding: 10px 20px;\r\n  display: block;\r\n  box-sizing: border-box;\r\n  width: 100%;\r\n  font-family: $font-family;\r\n  font-size: 12px;\r\n  line-height: 15px;\r\n  font-weight: bold;\r\n  text-decoration: none;\r\n  text-transform: uppercase;\r\n  color: #fff;\r\n  background: $gradient;\r\n  border: none;\r\n  border-radius: 22px;\r\n  cursor: pointer;\r\n\r\n  &:hover {\r\n    opacity: .5;\r\n  }\r\n\r\n  &_disabled {\r\n    opacity: .5;\r\n    cursor: not-allowed;\r\n  }\r\n\r\n  &_with-variant-outlined {\r\n    padding: 8px 18px;\r\n    color: $purple;\r\n    background: #fff;\r\n    border: 2px solid $purple;\r\n\r\n    &::before {\r\n      display: none;\r\n    }\r\n    &:hover {\r\n      opacity: 1;\r\n      background: #fff;\r\n      border: 2px solid #DDCEFF;\r\n    }\r\n  }\r\n\r\n  &_with-variant-text {\r\n    padding: 0;\r\n    color: $purple;\r\n    background: none;\r\n\r\n    &::before {\r\n      display: none;\r\n    }\r\n  }\r\n\r\n  &_with-icon {\r\n    position: relative;\r\n    padding: 14px 20px 15px;\r\n\r\n    &::after {\r\n      position: absolute;\r\n      top: 0%;\r\n      right: 0;\r\n      width: 44px;\r\n      height: 44px;\r\n      content: '';\r\n      background: url('./img/arrow_forward.svg') no-repeat;\r\n      background-position: center center;\r\n      transition: background .2s ease;\r\n    }\r\n\r\n    &:active {\r\n      &::after {\r\n        background-position: 65% center;\r\n      }\r\n    }\r\n  }\r\n\r\n  &_large {\r\n    padding: 13px 24px;\r\n  }\r\n\r\n  &_medium {\r\n    padding: 12px 18px;\r\n  }\r\n\r\n  &_higher {\r\n    padding: 14px 20px;\r\n  }\r\n}",".input {\r\n  display: flex;\r\n  width: 100%;\r\n\r\n  &:hover {\r\n    .input__base {\r\n      border-color: $darkShade75;\r\n    }\r\n\r\n    .input__button {\r\n      border-color: $darkShade75;\r\n    }\r\n  }\r\n\r\n  &__base {\r\n    padding: 9px 14px;\r\n    box-sizing: border-box;\r\n    width: 100%;\r\n    font-family: $font-family;\r\n    font-size: 14px;\r\n    line-height: 24px;\r\n    color: $darkShade75;\r\n    border: 1px solid $darkShade25;\r\n    border-radius: 4px;\r\n    outline: none;\r\n    transition: border .2s ease;\r\n    appearance: none;\r\n    \r\n    &::placeholder {\r\n      color: $darkShade25;\r\n      transition: color .2s ease;\r\n    }\r\n    \r\n    &:hover,\r\n    &:focus {\r\n      border-color: $darkShade75;\r\n\r\n      + .input__button {\r\n        border-color: $darkShade75;\r\n      }\r\n    }\r\n\r\n    &_with-button {\r\n      padding-right: 0;\r\n      border-right: none;\r\n      border-radius: 4px 0 0 4px;\r\n    }\r\n\r\n    &_focused {\r\n      border-color: $darkShade75;\r\n\r\n      &::placeholder {\r\n        color: $darkShade75;\r\n      }\r\n    }\r\n\r\n  }\r\n\r\n  &__button {\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n    width: 44px;\r\n    height: 44px;\r\n    background-color: transparent;\r\n    background-image: url('./img/arrow.svg');\r\n    background-repeat: no-repeat;\r\n    background-position: center center;\r\n    border: 1px solid $darkShade25;\r\n    border-left: none;\r\n    border-radius: 0 4px 4px 0;\r\n    outline: none;\r\n    transition: border .2s ease, background .1s ease;\r\n    cursor: pointer;\r\n  }\r\n}",".date-input {\r\n  position: relative;\r\n  display: flex;\r\n  width: 100%;\r\n  justify-content: space-between;\r\n\r\n  &__wrapper {\r\n    margin-right: 20px;\r\n    flex-grow: 1;\r\n\r\n    &:l